
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;1,100;1,200&display=swap');

* {
    margin: 0;
    padding: 0;
    font-family: 'Popins', sans-serif;
}




body {
    display: flex;
    justify-self: center;
    align-items: center;
    min-width: 100vh;
    background: #131313;
    /* flex-wrap: wrap; */
    align-content: center;
    justify-content: center;
    margin: 40px 0;
    font-family: Raleway;
    font-size: 14px;
    font-weight: 500;
    /* background-color: #bcaaa4; */
    -webkit-font-smoothing: antialiased;
    flex-direction: column;
}

.w-200 {
  width: 200px;
}

.card-bg {
  opacity: .5;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 0;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transition: 1s cubic-bezier(.445, .05, .55, .95), opacity 5s 1s cubic-bezier(.445, .05, .55, .95);
  pointer-events: none;
  border-radius: 20px;
}

div {
  cursor: url(data:image/png;base64,), auto !important;
}

.title {
  font-family: Raleway;
  font-size: 24px;
  font-weight: 700;
  color: #fff;
  text-align: center;
}

img {
  width: 300px;
}

.container {
  position: relative;
  display: flex;
  justify-self: center;
  align-items: center;
  flex-wrap: wrap;
  transform-style: preserve-3d;
  padding: 40px 80px;
}

.container .box {
  position: relative;
  width: 240px;
  height: 320px;
  margin: 40px;
  background: #232323;
  border-radius: 20px;
  transform-style: preserve-3d;

}

.container .box:hover {
  box-shadow: rgb(0 0 0 / 66%) 0 30px 60px 0, inset #333 0 0 0 5px, inset rgb(255 255 255 / 50%) 0 0 0 6px;
}


.container .box::before {
  /* content: 'NIKE'; */
  position: absolute;
  top: 20px;
  left: 20px;
  font-size: 5em;
  font-weight: 900;
  color: #fff;
  font-style: italic;
  opacity: 0;
  transition: 0.5s;
}

.container .box:hover::before,
.container .box:hover::after {
  opacity: 0.4;
}


.container .box::after {
  position: absolute;
  bottom: 20px;
  right: 20px;
  font-size: 3em;
  font-weight: 900;
  color: #fff;
  font-style: italic;
  opacity: 0;
  transition: 0.5s;
}

.container .box:nth-child(3)::after {
  position: absolute;
  bottom: 20px;
  left: 10px;
  font-size: 3em;
  font-weight: 900;
  color: #fff;
  font-style: italic;
  text-shadow: #000100 0 10px 10px;
  transition: 0.5s;
}

.container .box:nth-child(1)::after {
  content: 'Games';
}

.container .box:nth-child(3)::after {
  content: 'Hobbies';
}

.container .box:nth-child(2)::after {
  content: 'Apps';
}

.container .box .name {
  position: absolute;
  top: 0;
  left: 0;
  text-align: center;
  color: #fff;
  width: 100%;
  transform-style: preserve-3d;
  transform: translate3d(0, 0, 75px);
  transition: 0.5s;
  opacity: 0;
  z-index: 10;
}

.container .box:hover .name {
  top: 40px;
  opacity: 1;
}

.container .box .buy {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform-style: preserve-3d;
  transform: translate3d(-50%, 0, 75px);
  color: #fff;
  background: #333;
  padding: 10px 25px;
  border-radius: 30px;
  text-decoration: none;
  transition: 0.5s;
  opacity: 0;
  z-index: 10;
}

.container .box:hover .buy {
  bottom: 30px;
  opacity: 1;
}

.container .box .circle {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  transition: 0.5s;
  background: #fff;
  transform-style: preserve-3d;
  z-index: 10;
  opacity: 1;
  transform: translate3d(-50%, -50%, 9px);

}



.container .box .product {
  position: absolute;
  top: 50%;
  left: 33%;
  max-width: 300px;
  transition: 0.5s;
  z-index: 12;
  transition: 0.5;
  transform-style: preserve-3d;
  transform: translate3d(-50%, -50%, 10px) rotate(0deg);
  /* filter: drop-shadow(2px 4px 6px black); */
}

.container .box:hover .product {
  transform: translate3d(-50%, -50%, 20px) rotate(5deg);
}

.container .box:nth-child(1) .circle,
.container .box:nth-child(1) .buy {
  background: #379bf7;
}

.container .box:nth-child(3) .circle,
.container .box:nth-child(3) .buy {
  background: #9bdc28;
}

.container .box:nth-child(2) .buy {
  background: #fb4b4f;
}

